import React, { Component } from 'react';
import NewTask from './NewTask';
export default class Task extends Component {
    state = {
        tasks: [
            { name: "tarea 1", done: false },
            { name: "tarea 2", done: false },
            { name: "tarea 3", done: false },

        ],

// con localStorage queda mÃ¡s completo 
    }
    onCheked = (e) => {
        this.setState({
            tasks: (this.state.tasks.map(t => t.name === e.target.name ?
                { ...t, done: !t.done } :
                t))
        })
    }
    Delete=(e)=>{
        let tasks=this.state.tasks;
        let index=tasks.findIndex(t=>t.name===e.target.name);
        tasks.splice(index,1);
        
        
       this.setState({
           tasks:tasks
       })
    }
    Addtask = (task) => {
        if(!this.state.tasks.find(t=>t.name === task.name)&& task.name !== ""){
            this.setState({
                tasks: [...this.state.tasks, task]
            })
        }else{
            alert("Tarea Repetida o Invalida")
        }
    }
    Showtask = (tasks, done) => {
        return tasks
            .filter(t => t.done === done)
            .map((task,i) => (
                <tr key={i}>
                    <td>{task.name}</td>
                    <td><input type="checkbox" name={task.name} checked={task.done} onChange={this.onCheked} />
                    </td>
                    {task.done &&
                        <td>
                            <button className="btn btn-danger rounded" name={task.name}  onClick={this.Delete}>Borrar</button>
                            
                        </td>}

                </tr>
            )
            )
    }
    render() {
        return (
            <div className="container">
                <NewTask callback={this.Addtask}></NewTask>
                <table className="table table-dark col-md-10 mx-auto table-striped">
                    <thead>
                        <tr>
                            <th>Tarea</th>
                            <th>Hecha</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.Showtask(this.state.tasks, false)}
                    </tbody>
                </table>
                <h4 className="text-center">Tareas realizadas</h4>
                <table className="table table-dark col-md-10 mx-auto table-striped">
                    <thead>
                        <tr>
                            <th>Tarea</th>
                            <th>Hecha</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.Showtask(this.state.tasks, true)}
                    </tbody>
                </table>

            </div>
        )
    }
}
