import React, { Component } from 'react'

export default class NewTask extends Component {
    state={
        newTask:""
    }
    onChangeVal=(e)=>{
        this.setState({
            newTask:e.target.value
        })
    }
    newTask=()=>{
        var task={name:this.state.newTask,
            done:false}
            this.props.callback(task)
            this.setState({
                newTask:""
            })
            document.getElementById("nt").focus();
    }
    render() {
        return (
            <div className="container col-md-4 row p-2 mx-auto">
                <input id="nt" type="text"placeholder="nueva" value={this.state.newTask} onChange={this.onChangeVal} autoFocus />
                <button className="btn btn-success ml-2 rounded" onClick={this.newTask}>Crear</button>
            </div>
        )
    }
}
